<template>
  <a-row type="flex" justify="space-between">
    <a-col :span="11">
      <a-form-model-item :label="languageBase.width">
        <a-input-number
          v-model="value.width"
          :min="1"
          :max="1024"
          style="width: 100%;"
        />
      </a-form-model-item>
    </a-col>
    <a-col :span="11">
      <a-form-model-item :label="languageBase.height">
        <a-input-number v-model="value.height" :min="1" style="width: 100%;" />
      </a-form-model-item>
    </a-col>
  </a-row>
</template>

<script>
import _ from "lodash";
import { mapState } from "vuex";

export default {
  props: {
    value: Object
  },
  computed: {
    ...mapState({
      languageBase: store => store.languageBase.values
    })
  },
  watch: {
    "value.width"(val) {
      if (!_.isNumber(val) || this.value.width < 0) {
        this.value.width = 1;
      } else if (this.value.width > 1024) {
        this.value.width = 1024;
      }
    },
    "value.height"(val) {
      if (!_.isNumber(val) || this.value.height < 0) {
        this.value.height = 1;
      }
    }
  }
};
</script>
